.hero {
  width: 100%;
  height: 100vh;
  position: relative;
}

.hero-mid {
  width: 100%;
  height: 70vh;
  position: relative;
}
.hero-2 {
  width: 100%;
  height: 70vh;
  position: relative;
}
.hero-last {
  width: 100%;
  height: 70vh;
  position: relative;
}

.hero-mid h1 {
  color: #fff;
  padding-top: 4rem;
  font-family: -apple-system, BlinkMacSystemFont;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero-text {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.hero-text h1 {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}
.hero-text p {
  display: flex;
  text-align: start;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 1.2rem;
  font-weight: 900;
  color: #fff;
}
.hero-2 h1 {
  padding-bottom: 0.1rem;
  font-family: "Balsamiq Sans", cursive;
  color: #fff;
}
.hero-last h1 {
  font-weight: 800;
  font-size: 4rem;
  color: rgb(226, 243, 243);
  font-family: lobster;
}
@media screen and (max-width: 555px) {
  .hero-text p {
    font-size: 1rem;
    padding: 1rem 0 2rem 1rem;
  }
  .hero-text {
    padding-top: 8rem;
  }
  .hero h1 {
    font-size: 2.5rem;
    padding-bottom: 0rem;
  }
  .hero-2 h1 {
    padding-bottom: 3rem;
  }
}
