.NavbarItems {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
  width: 95%;
  height: 80px;
  border-radius: 10px;
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translate(-50%);
  background-color: rgb(190, 93, 228);
  z-index: 999;
}
.navbar-logo {
  color: #000;
  font-size: 2rem;
  cursor: pointer;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 12px;
  list-style: none;
  align-items: center;
}
.nav-links {
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0.7rem 1rem;
  white-space: nowrap;
}
.nav-links:hover {
  background-color: #5f2277;
  color: #fff;
  border-radius: 50px;
  transition: all 0.2s ease-in-out;
}
.nav-links-mobile {
  display: block;
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
  font-weight: 600;
  background-color: #fff;
  border-radius: 20px;
  width: 100%;
  transition: 0.1s ease-in-out;
  cursor: pointer;
  padding: 0.7rem 1rem;
}
.nav-links-mobile:hover {
  background: black;
  color: #fff;
  border-radius: 40px;
}

button {
  display: none;
}

button:hover {
  color: #fff;
  background-color: #222;
  display: none;
}
.menu-icons {
  display: none;
}

@media screen and (max-width: 850px) {
  .NavbarItems {
    z-index: 99;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #fff;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 0;
    align-items: stretch;
    padding: 80px 0 30px 0;
    margin: 0;
    transition: 0.3s ease-in-out;
    border-radius: 12px;
  }
  .nav-menu.active {
    left: 0;
    opacity: 1;
    z-index: -1;
    transition: 0.3s ease-in-out;
  }
  .nav-links {
    display: block;
    width: 100%;

    font-size: 1.2rem;
    font-weight: 1000;
    padding: 2rem 0;
  }
  .nav-links:hover {
    background-color: rgb(88, 193, 235);
    border-radius: 20px;
  }
  button {
    display: none;
  }
  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: auto;
    border-radius: 40px;
    width: 80%;
    background-color: black;
    text-decoration: none;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }
  .nav-links-mobile:hover {
    background-color: rgb(88, 193, 235);
  }
  .menu-icons {
    display: block;
  }
  .menu-icons i {
    font-size: 1.2rem;
    color: #222;
  }
}
