@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@700&family=Poppins:wght@200;300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.hero {
  width: 100%;
  height: 100vh;
  position: relative;
}

.hero-mid {
  width: 100%;
  height: 70vh;
  position: relative;
}
.hero-2 {
  width: 100%;
  height: 70vh;
  position: relative;
}
.hero-last {
  width: 100%;
  height: 70vh;
  position: relative;
}

.hero-mid h1 {
  color: #fff;
  padding-top: 4rem;
  font-family: -apple-system, BlinkMacSystemFont;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.hero-text {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
}
.hero-text h1 {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}
.hero-text p {
  display: flex;
  text-align: start;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 1.2rem;
  font-weight: 900;
  color: #fff;
}
.hero-2 h1 {
  padding-bottom: 0.1rem;
  font-family: "Balsamiq Sans", cursive;
  color: #fff;
}
.hero-last h1 {
  font-weight: 800;
  font-size: 4rem;
  color: rgb(226, 243, 243);
  font-family: lobster;
}
@media screen and (max-width: 555px) {
  .hero-text p {
    font-size: 1rem;
    padding: 1rem 0 2rem 1rem;
  }
  .hero-text {
    padding-top: 8rem;
  }
  .hero h1 {
    font-size: 2.5rem;
    padding-bottom: 0rem;
  }
  .hero-2 h1 {
    padding-bottom: 3rem;
  }
}

.NavbarItems {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
  width: 95%;
  height: 80px;
  border-radius: 10px;
  position: fixed;
  top: 10px;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  background-color: rgb(190, 93, 228);
  z-index: 999;
}
.navbar-logo {
  color: #000;
  font-size: 2rem;
  cursor: pointer;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 12px;
  list-style: none;
  align-items: center;
}
.nav-links {
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0.7rem 1rem;
  white-space: nowrap;
}
.nav-links:hover {
  background-color: #5f2277;
  color: #fff;
  border-radius: 50px;
  transition: all 0.2s ease-in-out;
}
.nav-links-mobile {
  display: block;
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
  font-weight: 600;
  background-color: #fff;
  border-radius: 20px;
  width: 100%;
  transition: 0.1s ease-in-out;
  cursor: pointer;
  padding: 0.7rem 1rem;
}
.nav-links-mobile:hover {
  background: black;
  color: #fff;
  border-radius: 40px;
}

button {
  display: none;
}

button:hover {
  color: #fff;
  background-color: #222;
  display: none;
}
.menu-icons {
  display: none;
}

@media screen and (max-width: 850px) {
  .NavbarItems {
    z-index: 99;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #fff;
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    left: -100%;
    opacity: 0;
    align-items: stretch;
    padding: 80px 0 30px 0;
    margin: 0;
    transition: 0.3s ease-in-out;
    border-radius: 12px;
  }
  .nav-menu.active {
    left: 0;
    opacity: 1;
    z-index: -1;
    transition: 0.3s ease-in-out;
  }
  .nav-links {
    display: block;
    width: 100%;

    font-size: 1.2rem;
    font-weight: 1000;
    padding: 2rem 0;
  }
  .nav-links:hover {
    background-color: rgb(88, 193, 235);
    border-radius: 20px;
  }
  button {
    display: none;
  }
  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: auto;
    border-radius: 40px;
    width: 80%;
    background-color: black;
    text-decoration: none;
    color: #fff;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }
  .nav-links-mobile:hover {
    background-color: rgb(88, 193, 235);
  }
  .menu-icons {
    display: block;
  }
  .menu-icons i {
    font-size: 1.2rem;
    color: #222;
  }
}

.destination {
  margin: 4rem 6rem;
}
.destination h1 {
  font-size: 3rem;
  background-color: black;
  color: #fff;
  border-radius: 10px;
}
.destination p {
  font-weight: 600;
  padding-top: 10px;
}
.first-des {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 4.5rem;
}
.first-des-reverse {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  margin-top: 4.5rem;
}
.des-text {
  width: 45%;
  text-align: start;
  font-size: 1.1rem;
}
.des-text h2 {
  padding-bottom: 1rem;
  text-decoration: underline;
}

.image {
  position: relative;
  width: 50%;
  display: flex;
  justify-content: space-between;
  z-index: -99;
}

.image img {
  width: 49%;
  height: 350px;
  object-fit: cover;
  border-radius: 6px;
  box-shadow: -1px 1px 62px -18px rgba(0 0 0 0.2);
}
.image img:nth-child(2) {
  position: absolute;
  top: -10%;
  right: 0;
}

@media screen and (max-width: 850px) {
  .destination {
    margin: 4rem 2rem;
  }
  .destination h1 {
    font-size: 2.5rem;
  }
  .first-des {
    display: flex;
    flex-direction: column-reverse;
    margin-top: 4.5rem;
  }
  .des-text {
    width: 100%;
  }
  .image {
    width: 100%;
    margin: 1.8rem 0;
  }
  .image img {
    height: 250px;
  }
}

.trip {
  margin: 4rem 6rem;
}

.trip h1 {
  font-size: 3rem;
}
.tripcard {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.t-card {
  width: 32%;
  text-align: start;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  cursor: pointer;
  padding: 1rem 0.8rem;
}
.t-image {
  height: 200px;
  overflow: hidden;
  border-radius: 7px;
}
.t-image img {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
  border-radius: 7px;
}
.t-image:hover img {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.t-card h4 {
  font-size: 1.3rem;
  padding: 0.9rem 0 0.2rem 0;
}

@media screen and (max-width: 850px) {
  .trip {
    margin: 4rem 2rem;
  }
  .tripcard {
    flex-direction: column;
  }
  .t-card {
    width: 100%;
    margin-bottom: 1.5rem;
  }
}

