.sign {
  margin: 4rem 6rem;
  color: #2a2a2a;
  background-color: rgb(212, 235, 231);
}

.sign form {
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  width: 50%;
  margin: auto;
}
.sign input {
  height: 3rem;
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: 0.3rem;
  border: 1px solid #2a2a2a;
}

.sign textarea {
  padding: 0 1rem;
  margin-bottom: 2rem;
  border-radius: 0.3rem;
  border: 1px solid #2a2a2a;
}
.sign button {
  display: inline;
  margin-bottom: 1rem;
  border: 0.5px solid;
}

@media screen and (max-width: 850px) {
  .sign {
    margin: 4rem 2rem;
  }
  .sign form {
    padding-top: 2rem;
    width: 70%;
  }
}
