.founder {
  margin: 4rem 6rem;
}

.founder h1 {
  font-size: 3rem;
}
.foundercard {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.l-card {
  width: 32%;
  text-align: start;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  cursor: pointer;
  padding: 1rem 0.8rem;
  background-color: #87d9ed;
}
.l-image {
  height: 200px;
  overflow: hidden;
  border-radius: 7px;
}
.l-image img {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
  border-radius: 7px;
}
.l-image:hover img {
  transform: scale(1.3);
}

.l-card h4 {
  font-size: 1.3rem;
  padding: 0.9rem 0 0.2rem 0;
}

.about-container h1 {
  font-size: 2rem;
}
.about-container p {
  text-align: start;
  font-family: serif;
  margin-left: 5px 5px 5px 5px;
}
h3 {
  padding-top: 15px;
}
.h {
  padding: 10px 0 10px 0;
}

@media screen and (max-width: 850px) {
  .founder {
    margin: 4rem 2rem;
  }
  .foundercard {
    flex-direction: column;
  }
  .l-card {
    width: 100%;
    margin-bottom: 1.5rem;
  }
  .founder h1 {
    font-size: 2.5rem;
    text-align: start;
    text-overflow: hidden;
  }
  .founder p {
    text-align: start;
    font-weight: 500;
  }
}
