.service {
  margin: 4rem 6rem;
}

.service h1 {
  font-size: 3rem;
}
.servicecard {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.s-card {
  width: 32%;
  text-align: start;
  box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0.11);
  border-radius: 7px;
  cursor: pointer;
  padding: 1rem 0.8rem;
}
.s-image {
  height: 200px;
  overflow: hidden;
  border-radius: 7px;
}
.s-image img {
  width: 100%;
  height: 100%;
  transition: 0.3s ease-in-out;
  border-radius: 7px;
}
.s-image:hover img {
  transform: scale(1.3);
}

.s-card h4 {
  font-size: 1.3rem;
  padding: 0.9rem 0 0.2rem 0;
}
p {
  font-size: 1.5rem;
  font-weight: 600;
}
.q {
  font-family: lobs;
  background-color: hotpink;
}
.hero-last p {
  font-size: 2rem;
  color: indigo;
  font-family: transform;
  text-align: center !important;
}

@media screen and (max-width: 850px) {
  .service {
    margin: 4rem 2rem;
  }
  .servicecard {
    flex-direction: column;
  }
  .s-card {
    width: 100%;
    margin-bottom: 1.5rem;
  }
}
